 body {
     background-color: var(--wp--preset--color--base);
     color: var(--wp--preset--color--contrast);
     font-family: notobold;
     font-size: clamp(14px, 0.875rem +((1vw - 3.2px) * 0.455), 18px);
     font-weight: var(--wp--custom--typography--font-weight--normal);
     line-height: 1.15;
     margin-top: 0px;
     margin-right: 0px;
     margin-bottom: 0px;
     margin-left: 0px;
     --wp--style--root--padding-top: 0px;
     --wp--style--root--padding-right: var(--wp--preset--spacing--15);
     --wp--style--root--padding-bottom: 0px;
     --wp--style--root--padding-left: var(--wp--preset--spacing--15);
 }

 a:where(:not(.wp-element-button)) {
     color: var(--wp--preset--color--primary);
     text-decoration: none;
 }

 :root :where(a:where(:not(.wp-element-button)):hover) {
     text-decoration: none;
 }

 :root :where(a:where(:not(.wp-element-button)):focus) {
     text-decoration: none;
 }

 :root :where(a:where(:not(.wp-element-button)):active) {
     color: var(--wp--preset--color--secondary);
     text-decoration: none;
 }

 h1,
 h2,
 h3,
 h4,
 h5,
 h6 {
     font-weight: 400;
     line-height: 1;
 }

 h1 {
     color: var(--wp--preset--color--contrast);
     font-family: var(--wp--preset--font-family--tt-commonsdemibold);
     font-size: var(--wp--custom--typography--font-size--heading-1);
     font-weight: var(--wp--custom--typography--font-weight--normal);
     line-height: var(--wp--custom--typography--line-height--heading-1);
 }

 h2 {
     color: var(--wp--preset--color--contrast);
     font-family: var(--wp--preset--font-family--tt-commonsdemibold);
     font-size: var(--wp--custom--typography--font-size--heading-2);
     font-weight: var(--wp--custom--typography--font-weight--normal);
     line-height: var(--wp--custom--typography--line-height--heading-2);
 }

 h3 {
     color: var(--wp--preset--color--contrast);
     font-family: var(--wp--preset--font-family--tt-commonsdemibold);
     font-size: var(--wp--custom--typography--font-size--heading-3);
     font-weight: var(--wp--custom--typography--font-weight--normal);
     line-height: var(--wp--custom--typography--line-height--heading-3);
 }

 h4 {
     color: var(--wp--preset--color--contrast);
     font-family: var(--wp--preset--font-family--tt-commonsdemibold);
     font-size: var(--wp--custom--typography--font-size--heading-4);
     font-weight: var(--wp--custom--typography--font-weight--normal);
     line-height: var(--wp--custom--typography--line-height--heading-4);
 }

 h5 {
     color: var(--wp--preset--color--contrast);
     font-family: var(--wp--preset--font-family--tt-commonsdemibold);
     font-size: var(--wp--custom--typography--font-size--heading-5);
     font-weight: var(--wp--custom--typography--font-weight--normal);
     line-height: var(--wp--custom--typography--line-height--heading-5);
 }

 h6 {
     color: var(--wp--preset--color--contrast);
     font-family: var(--wp--preset--font-family--tt-commonsdemibold);
     font-size: var(--wp--custom--typography--font-size--heading-6);
     font-weight: var(--wp--custom--typography--font-weight--normal);
     line-height: var(--wp--custom--typography--line-height--heading-6);
 }

 :root :where(.wp-element-button, .wp-block-button__link) {
     background-color: var(--wp--preset--color--primary);
     border-radius: 2px;
     border-width: 0;
     color: var(--wp--preset--color--base);
     font-family: inherit;
     font-size: inherit;
     line-height: inherit;
     padding: calc(0.667em + 2px) calc(1.333em + 2px);
     text-decoration: none;
 }






