.mdFooter .mdFooter-location-box p a {
    font-size: 16px;
    line-height: 16px;
    margin: 0;
    color: #000;
    letter-spacing: -1px
}

.mdFooter .mdFooter-location-box:hover,
.mdFooter .mdFooter-location-box:focus {
    -webkit-transition: all .3s linear;
    -o-transition: all .3s linear;
    transition: all .3s linear
}

.mdFooter .mdFooter-location-box:hover .mdFooter-location-hover,
.mdFooter .mdFooter-location-box:focus .mdFooter-location-hover {
    -webkit-transform: translate(0%, 0%) !important;
    transform: translate(0%, 0%) !important;
    opacity: 1;
    visibility: visible;
    -webkit-transition: all .3s linear;
    -o-transition: all .3s linear;
    transition: all .3s linear
}

.mdFooter .mdFooter-location-hover {
    background: #fff;
    width: 272px;
    height: calc(100% + 4px);
    border: 2px solid #fff;
    z-index: 999;
    position: absolute;
    top: -2px;
    text-align: center;
    padding: 10px 35px;
    box-shadow: 5px 0 20px rgba(0, 0, 0, .1);
    opacity: 0;
    visibility: hidden;
    -webkit-transition: all .3s linear;
    -o-transition: all .3s linear;
    transition: all .3s linear
}

.mdFooter .mdFooter-location-hover p {
    line-height: 30px;
    font-size: 14px;
    font-family: 'noto_sansmedium', sans-serif;
    color: #4c5056;
    letter-spacing: normal;
    position: relative;
    top: 50%;
    -webkit-transform: translate(0, -50%);
    -moz-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);
    margin: 0 auto;
    text-align: center
}

.mdFooter .mdFooter-location-hover:after {
    content: '';
    width: 0;
    height: 0;
    position: absolute;
    top: 50%;
    -webkit-transform: translate(0, -50%);
    -moz-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%)
}

.mdFooter .mdFooter-location-box.virginia-box:hover,
.mdFooter .mdFooter-location-box.virginia-box:focus {
    background: #fef6c7;
    border-color: #fef6c7
}

.mdFooter .mdFooter-location-box.virginia-box .mdFooter-location-hover {
    left: 100%;
    -webkit-transform: translate(5%, 0%);
    transform: translate(5%, 0%)
}

.mdFooter .mdFooter-location-box.virginia-box .mdFooter-location-hover:after {
    left: -14px;
    border-bottom: 14px solid transparent;
    border-top: 14px solid transparent;
    border-right: 14px solid #fff
}

.mdFooter .mdFooter-location-box.virginia-box svg {
    width: 67px;
