.mdinc-video-play-button-box:after,
.mdinc-video-play-button-box-inner {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    border: 2px solid rgba(255, 255, 255, .3);
