    display: flex;
    flex-wrap: wrap;
    gap: 40px
}

body.search .search-result-boxes-main li {
    padding: 0;
    width: calc(25% - 30px);
    box-shadow: 0px 4px 40px 0px #0000001A;
    border-radius: 10px;
    overflow: hidden;
    margin: 0
}

body.search .search-result-boxes-main li .wp-block-group.has-global-padding {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
    padding-left: 0 !important;
    padding-right: 0 !important
}

body.search .search-result-boxes-main li figure {
    aspect-ratio: inherit !important
}

body.search .search-result-boxes-main li figure a {
    display: block;
    width: 100%
}

body.search .search-result-boxes-main li figure a img {
    height: 250px !important
}

body.search .search-result-boxes-main li .wp-block-post-title {
    margin: 0 !important;
    padding: 20px 20px 15px
}

body.search .search-result-boxes-main li .wp-block-post-title a {
    font-size: 26px;
    line-height: normal;
    display: table;
    text-decoration: none
}

body.search .search-result-boxes-main li .wp-block-post-date {
    margin: 0 !important;
    padding: 0 20px 20px
}

body.search .search-result-boxes-main li .wp-block-post-date time a {
    font-size: 16px;
    line-height: 18px;
    font-weight: 500;
    font-family: 'tt_commonsmedium', sans-serif;
    color: #4c5056;
    text-decoration: none
}

body.search .wp-block-query:has(.search-result-not-found-text) {
    padding: 0;
    margin: 0;
    max-width: 1200px;
    margin: 0 auto
}

body.search .search-result-not-found-text:not(:has(.wp-block-query-no-results)) {
    padding: 0 !important
}

body.search .search-result-not-found-text {
    padding: 0 !important;
    margin: 0 !important
}

body.search .search-result-not-found-text p {
    font-size: 24px;
    line-height: 34px;
    font-weight: 500;
    font-family: 'tt_commonsmedium', sans-serif;
    text-align: center;
    max-width: 750px;
    margin: 0 auto 30px
}

body.search .search-result-pagination-main {
    margin: 0;
    padding: 0
}

body.search .search-result-pagination-main .wp-block-query-pagination {
    margin: 60px 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px
}

body.search .search-result-pagination-main .wp-block-query-pagination-numbers {
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px
}

body.search .search-result-pagination-main .wp-block-query-pagination-numbers .page-numbers {
    font-size: 24px;
    line-height: 1;
    border-radius: 6px;
    padding: 9px 15px 6px;
    margin: 0;
    font-weight: 400;
    text-decoration: none
}

body.search .search-result-pagination-main .wp-block-query-pagination-numbers .page-numbers.current {
    background: #0b1014;
    margin: 0;
    color: #fff
}

body.search .search-result-pagination-main .wp-block-query-pagination-previous,
body.search .search-result-pagination-main .wp-block-query-pagination-next {
    background-image: url(/wp-content/themes/career-fse/assets/svg/readmore-right-arrow.svg);
    width: 20px;
    height: 20px;
    background-repeat: no-repeat;
    background-size: contain;
    text-indent: -99999px;
    margin: 0 !important;
    background-position: center
}

body.search .search-result-pagination-main .wp-block-query-pagination-previous {
    transform: rotate(180deg);
    background-position: center
}

body.search .mdinc-search-count {
    font-size: 30px;
    font-weight: 400;
    line-height: 1;
    text-align: left;
    margin-bottom: 20px;
    color: #000
}

body.search:has(.wp-block-query-no-results) .mdinc-search-count {
    display: none
}

