.open-position h2.title {
    text-align: center;
    margin-bottom: 16px
}

.open-position p.details {
    text-align: center;
    margin-bottom: 65px
}

.open-position .open-position-accordion {
    max-width: 100%;
    width: 970px;
    margin: 0 auto
}

.open-position-accordion .position-heading {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #d7d7d7
}

.open-position-accordion .position-heading p {
    font-size: 25px;
    color: #0b0b0b;
    font-weight: 400;
    margin-top: 0;
    line-height: 30px;
    margin-bottom: 16px;
    font-family: 'tt_commonsmedium', sans-serif;
    padding-bottom: 0
}

.positions-lists .position-details-main {
    font-family: "-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen-Sans,Ubuntu,Cantarell", "Helvetica Neue", sans-serif;
    font-weight: 400;
    color: #000;
    position: relative;
    /* border-bottom: 1px solid #d7d7d7; */
    transition: all .5s
}

.positions-lists .position-details-main.active {
    /* border-bottom: 2px solid #fd5636; */
    transition: all .5s
}

.position-details-main .position-details-header {
    padding: 23px 0
}

.position-details-header h4.position-name {
    font-size: 24px;
    color: #0b0b0b;
    font-weight: 400;
    margin-top: 0;
    line-height: 30px;
    margin-bottom: 0;
    font-family: 'tt_commonsmedium', sans-serif
}

.position-details-header p.job-location {
    margin-bottom: 0;
    color: #000
}

.position-details-main .position-details-header,
.position-details-body {
    display: flex;
    justify-content: space-between
}

.position-details-main .position-details-body {
    text-align: left;
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    padding-bottom: 23px;
    visibility: hidden;
    position: absolute
}

.position-details-main .position-details-body.active {
    max-height: 100%;
    opacity: 1;
    position: static;
    visibility: visible
}

.position-details-body p.position-apply-btn a {
    border-bottom: 1px solid #fd5636;
    line-height: 30px;
    font-family: 'noto_sansmedium', sans-serif
}

.view-all-jobs {
    background: #fd5636;
    color: #fff;
    font-size: 16px;
    line-height: 18px;
    box-shadow: 0 15px 20px 0 rgb(0 0 0/10%);
    padding: 19px 26px 15px;
    text-align: center;
    cursor: pointer;
    font-family: noto_sansbold, sans-serif;
    outline: 0;
    margin: 0 auto !important;
    display: table;
    -webkit-appearance: none;
    text-decoration: none
}

.view-all-jobs a {
    color: #fff
}

.career-councelling-page-main .mdcareer-councelling-sidebar-box.councelling-opening-section {
    box-shadow: 0px 3px 6px 1px rgba(0, 0, 0, .16);
    -webkit-box-shadow: 0px 3px 6px 1px rgba(0, 0, 0, .16);
    -moz-box-shadow: 0px 3px 6px 1px rgba(0, 0, 0, .16)
}

.career-councelling-page-main .councelling-opening-section {
    padding: 36px 40px 30px;
    margin-bottom: 40px
}

.councelling-opening-section .job-listing-sidebar-section h4 {
    position: relative
}

.councelling-opening-section .job-listing-sidebar-section h4:before {
    content: "";
    border-left: 4px solid #fd5636;
    margin-left: -40px;
    position: absolute;
    width: 4px;
    height: 100%
}

.councelling-opening-section .job-listing-sidebar-section h3 {
    line-height: 30px;
    margin-bottom: 0;
    padding-right: 10px
}

.councelling-opening-section .job-listing-sidebar-section h3 a {
    font-family: 'tt_commonsmedium', sans-serif;
    font-size: 20px;
    line-height: 30px;
    color: #000;
    text-decoration: none
}

.councelling-opening-section .job-listing-sidebar-section .job-data-warpper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    border-bottom: 2px solid #fd5636;
    padding-bottom: 20px;
    margin-bottom: 10px
}

.wp-image-xxxx {
    height: 60px;
}

.mdinc-rating .has-4-columns {
    gap: 40px;
}

.position-details-bodyn {
    display: flex;
    justify-content: space-between;
}

.position-details-bodyn .position-detail-description {
    width: 80%;
    font-family: 'noto_sansregular', sans-serif;
    margin-bottom: 0
}

.position-details-bodyn p.position-apply-btn a {
    border-bottom: 2px solid #fd5636;
    line-height: 30px;
    font-family: 'noto_sansmedium', sans-serif
}

.position-details-bodyn p.position-apply-btn {
    margin-bottom: 0
}

.accordion-item {
    border-bottom: 1px solid #e0e0e0;
    transition: border-color 0.3s ease;
}

.accordion-item.active {
    border-bottom: 2px solid #fd5636;}

/* Remove default details marker */
details>summary {
    list-style: none;
}

details>summary::-webkit-details-marker {
    display: none;
}

